function trigoSeries(cnt, strength) {
  var data = [];
  for (var i = 0; i < cnt; i++) {
    data.push(
      (Math.sin(i / strength) * (i / strength) + i / strength + 1) *
      (strength * 2)
    );
  }

  return data;
}

